}


/* Attempt to get chat looking like an actual chat (iMessage)*/
#chatbox {
  overflow:   none;
  position:   relative;
  /* width:      100%;
  height:     65vh;  Inhere*/
}
#chatmessages
{
    overflow:   auto;
    position:   absolute;
    bottom:     0;
    width: 100%;
    max-height: 65vh;
}


